// --- IMPORTANT: Replace with your actual Firebase config ---
const firebaseConfig = {
  apiKey: process.env.REACT_APP_FIREBASE_API_KEY,
  authDomain: process.env.REACT_APP_FIREBASE_AUTH_DOMAIN,
  projectId: process.env.REACT_APP_FIREBASE_PROJECT_ID,
  storageBucket: process.env.REACT_APP_FIREBASE_STORAGE_BUCKET,
  messagingSenderId: process.env.REACT_APP_FIREBASE_MESSAGING_SENDER_ID,
  appId: process.env.REACT_APP_FIREBASE_APP_ID
};

// Optionally, throw if any are missing (for dev safety)
Object.entries(firebaseConfig).forEach(([key, value]) => {
  if (!value) {
    throw new Error(`Missing Firebase config value for ${key}. Did you set your .env file?`);
  }
});

const app = initializeApp(firebaseConfig);
// eslint-disable-next-line @typescript-eslint/no-unused-vars
const auth = getAuth(app); // Needed for Firestore permissions, silence unused warning
const db = getFirestore(app);

const ToolApp: React.FC<ToolAppProps> = ({ user, org, permissions, logAction }) => {
  // Log received props, especially logAction
  console.log('ToolApp Props Received:', { user, orgId: org?.id, permissions, logActionExists: !!logAction });

  // Row selection state
  const [selectedRowIndex, setSelectedRowIndex] = useState<number>(-1);

  const [allItems, setAllItems] = useState<StockItem[]>([]);
  const [loading, setLoading] = useState<boolean>(true);
  const [error, setError] = useState<string | null>(null);
  const [searchTerm, setSearchTerm] = useState<string>('');
  const [currentFilter, setCurrentFilter] = useState<ItemStatus | 'All'>('All'); // Default filter
  const [selectedItemDetails, setSelectedItemDetails] = useState<StockItem | null>(null); // For View Details Modal
  const [isAddItemModalOpen, setIsAddItemModalOpen] = useState<boolean>(false); // State for modal visibility
  const [showFlaggedOnly, setShowFlaggedOnly] = useState<boolean>(false); // State for flag toggle
  const [editingItem, setEditingItem] = useState<StockItem | null>(null); // State to hold item being edited
  // State for new modals
  const [reportingIssueItem, setReportingIssueItem] = useState<StockItem | null>(null);
  const [addingUpdateItem, setAddingUpdateItem] = useState<StockItem | null>(null);
  const [resolvingIssueItem, setResolvingIssueItem] = useState<StockItem | null>(null);
  const [isSettingsModalOpen, setIsSettingsModalOpen] = useState(false); // State for settings modal
  const [itemForNote, setItemForNote] = useState<StockItem | null>(null); // New state for item context for general note
  const [isUserActivityViewOpen, setIsUserActivityViewOpen] = useState<boolean>(false); // State for user activity view

  // State for column configuration (order matters now)
  const [visibleColumnIds, setVisibleColumnIds] = useState<Array<ColumnConfig['id']>>(() => {
    try {
      const stored = localStorage.getItem(LOCAL_STORAGE_KEY_VISIBLE_COLUMNS);
      // Ensure stored value is an array, otherwise use default
      const parsed = stored ? JSON.parse(stored) : DEFAULT_VISIBLE_COLUMN_IDS;
      return Array.isArray(parsed) ? parsed : DEFAULT_VISIBLE_COLUMN_IDS;
    } catch (error) {
      console.error("Error reading visible columns from localStorage", error);
      return DEFAULT_VISIBLE_COLUMN_IDS;
    }
  });
  
  // Save visible columns to localStorage whenever they change
  useEffect(() => {
    try {
        localStorage.setItem(LOCAL_STORAGE_KEY_VISIBLE_COLUMNS, JSON.stringify(visibleColumnIds));
    } catch (error) {
        console.error("Error saving visible columns to localStorage", error);
    }
  }, [visibleColumnIds]);

  // Derive visible columns based on IDs state (order is now determined by visibleColumnIds)
  const visibleColumns = visibleColumnIds
      .map(id => ALL_COLUMNS.find(col => col.id === id))
      .filter((col): col is ColumnConfig => col !== undefined); // Filter out undefined if an ID becomes invalid

  // Update filtering logic to include flag toggle
  const filteredItems = allItems.filter(item => { // filteredItems is the displayed array

    const matchesSearch = 
      !searchTerm || 
      item.deliveryName.toLowerCase().includes(searchTerm.toLowerCase()) ||
      item.productName.toLowerCase().includes(searchTerm.toLowerCase());
      
    let matchesFilter = false;
    if (currentFilter === 'All') {
        matchesFilter = true;
    } else if (currentFilter === 'Late') {
        // Show items that are pending and meet the late criteria
        matchesFilter = isItemLate(item);
    } else {
        // Standard status matching, but don't show late items unless 'Late' or 'All' filter is selected
        // Or should late items still appear in Pending Delivery? Let's keep them there for now.
        matchesFilter = item.currentStatus === currentFilter;
    }

    // Apply flag filter if toggle is on
    const matchesFlag = !showFlaggedOnly || item.isFlagged === true;

    return matchesFilter && matchesSearch && matchesFlag; // Add flag condition
  });

  // Clamp selectedRowIndex if filteredItems changes
  useEffect(() => {
    setSelectedRowIndex(idx => {
      if (filteredItems.length === 0) return -1;
      if (idx < 0) return 0;
      if (idx >= filteredItems.length) return filteredItems.length - 1;
      return idx;
    });
  }, [filteredItems]);

  // --- Firestore Real-time Sync ---
  useEffect(() => {
    if (!org?.id) return;
    setLoading(true);
    setError(null);
    const q = query(collection(db, 'orgs', org.id, 'stockItems'), orderBy('orderDate', 'desc'));
    const unsub = onSnapshot(q, (snapshot) => {
      const items: StockItem[] = snapshot.docs.map(docSnap => ({ id: docSnap.id, ...docSnap.data() })) as StockItem[];
      setAllItems(items);
      setLoading(false);
    }, (err) => {
      setError('Failed to load items: ' + err.message);
      setLoading(false);
    });
    return () => unsub();
  }, [org?.id]); // db is stable, remove from deps

  // Placeholder functions for actions
  const handleSearch = (term: string) => {
    setSearchTerm(term);
  };


  const handleFilterChange = (filter: ItemStatus | 'All') => {
    setCurrentFilter(filter);
  };

  // Centralized function to update item in Firestore and add log entry
  const updateItemAndLog = useCallback(async (itemId: string, userId: string | undefined, changes: Partial<StockItem>, event: ActivityEvent) => {
    if (!db || !org?.id || !userId) {
      console.error('Cannot update item: Missing configuration.', { db: !!db, orgId: org?.id, userId });
      setError('Cannot update item: Missing configuration.');
      return;
    }

    const itemRef = doc(db, 'orgs', org.id, 'stockItems', itemId);
    const itemBefore = allItems.find(i => i.id === itemId);
    if (!itemBefore) {
        console.error(`Item ${itemId} not found for update`);
        setError(`Item ${itemId} not found.`);
        return;
    }

    // Combine updates with new activity event and timestamp
    const finalUpdates = removeUndefinedFields({
      ...changes,
      lastUpdated: serverTimestamp(),
    });

    const itemAfter = { ...itemBefore, ...finalUpdates }; // Approximate state after update

    try {
      await updateDoc(itemRef, finalUpdates);
      console.log(`Item ${itemId} updated successfully.`);

      // Log the generic update action
      console.log('updateItemAndLog: Attempting to log action. logAction available?', !!logAction);
      if (logAction) {
        // Determine standardized action name
        let actionName: string;
        try {
          if (event.type === 'EDITED') {
            actionName = 'item.update.fields';
          } else if (event.type === 'FLAG_TOGGLED') {
            actionName = 'item.update.flag';
          } else if (event.type === 'STATUS_CHANGED') {
            const newStatus = event.details?.newStatus?.toLowerCase() || 'unknown';
            actionName = `item.update.status.${newStatus}`;
          } else if (event.type === 'ISSUE_REPORTED') {
            actionName = 'item.update.issue.report';
          } else if (event.type === 'ISSUE_RESOLVED') {
            actionName = 'item.update.issue.resolve';
          } else if (event.type === 'ISSUE_UPDATE_ADDED') {
            actionName = 'item.update.issue.update';
          } else if (event.type === 'NOTE_ADDED') {
            actionName = 'item.add_note';
          } else {
            actionName = event.type; // Fallback
          }
        } catch (nameError) {
          console.error('Error deriving action name for logging:', nameError);
          actionName = event.type;
        }

        // Construct the data payload ensuring types match LogActionData
        const logData: LogActionData = {
           orgId: org.id,
           toolKey: 'incomingtool',
           action: actionName, // actionName is now guaranteed to be string
           entityPath: `stockItems/${itemId}`,
           // Conditionally add before/after/details if they exist
           ...(itemBefore !== undefined && { before: itemBefore }),
           ...(itemAfter !== undefined && { after: itemAfter }),
           ...(event.details !== undefined && { details: event.details })
        };

        await logAction(logData); // Pass the correctly typed object

      }

    } catch (e) {
      console.error(`Error updating item ${itemId}:`, e);
      setError(`Failed to update item: ${(e as Error).message}`);
      throw e; // Re-throw error to be caught by calling handler if needed
    }
  }, [org, allItems, logAction]);

  // Update handleItemAction to dispatch to specific handlers or generic updates
  const handleItemAction = useCallback(async (itemId: string, action: ActionDefinition) => {
    console.log(`Action: ${action.label} on item ${itemId}`);
    const item = allItems.find(i => i.id === itemId);
    if (!item) return;

    switch (action.id) {
      case 'Edit Item':
        setEditingItem(item);
        setIsAddItemModalOpen(true);
        break;
      case 'Flag Item':
      case 'Unflag Item':
        const newFlagState = action.id === 'Flag Item';
        await updateItemAndLog(itemId, user?.uid, 
          { isFlagged: newFlagState }, 
          createActivityEvent(user?.uid || '', 'FLAG_TOGGLED', { isFlagged: newFlagState })
        );
        break;
      case 'Mark as Delivered':
        if (item.currentStatus !== 'Delivered') {
          await updateItemAndLog(itemId, user?.uid,
            { currentStatus: 'Delivered', dateDelivered: new Date().toISOString() },
            createActivityEvent(user?.uid || '', 'STATUS_CHANGED', { previousStatus: item.currentStatus, newStatus: 'Delivered' })
          );
        }
        break;
      case 'Archive':
         if (item.currentStatus !== 'Archived') {
          await updateItemAndLog(itemId, user?.uid, 
            { currentStatus: 'Archived' },
            createActivityEvent(user?.uid || '', 'STATUS_CHANGED', { previousStatus: item.currentStatus, newStatus: 'Archived' })
          );
         }
        break;
      case 'Add Note': // New case for adding a general note
        setItemForNote(item); // Set the item context for the modal
        break;
      case 'Report Issue': // Close detail view & open report modal
        setSelectedItemDetails(null);
        setReportingIssueItem(item);
        break;
      case 'Resolve Issue': // Close detail view & open resolve modal
        setSelectedItemDetails(null);
        setResolvingIssueItem(item);
        break;
      case 'Add Issue Update': // Close detail view & open add-note modal
        setSelectedItemDetails(null);
        setAddingUpdateItem(item);
        break;
      case 'View Details': // Open item detail modal
        setSelectedItemDetails(item);
        break;
      // Default case for actions handled elsewhere or needing no state change here (like View Details)
    }
  }, [user?.uid, allItems, updateItemAndLog, setEditingItem, setIsAddItemModalOpen, setReportingIssueItem, setAddingUpdateItem, setResolvingIssueItem, setSelectedItemDetails]);

  // Handler for reporting an issue
  const handleSaveReportIssue = useCallback(async (description: string) => {
    if (!reportingIssueItem || !org?.id) return;
    const itemId = reportingIssueItem.id;
    try {
      await updateItemAndLog(
        itemId,
        user?.uid,
        { currentStatus: 'Issue', issueDescription: description },
        createActivityEvent(user?.uid || '', 'ISSUE_REPORTED', { issueDescription: description })
      );
      setReportingIssueItem(null);
    } catch (err) {
      setError('Failed to report issue: ' + (err as Error).message);
    }
  }, [reportingIssueItem, org?.id, user?.uid, updateItemAndLog, setReportingIssueItem, setError]);

  // Handler for adding an issue update note
  const handleSaveIssueUpdate = useCallback(async (note: string) => {
    if (!addingUpdateItem) return;
    try {
      await updateItemAndLog(
        addingUpdateItem.id,
        user?.uid,
        {},
        createActivityEvent(user?.uid || '', 'ISSUE_UPDATE_ADDED', { note })
      );
      setAddingUpdateItem(null); // Close modal
    } catch (err) {
      // Error is already logged by updateItemAndLog
      // setError('Failed to add issue update: ' + (err as Error).message);
    }
  }, [addingUpdateItem, updateItemAndLog, setAddingUpdateItem, user?.uid]);

  // Handler for resolving an issue
  const handleSaveResolveIssue = useCallback(async (outcome: string, note?: string) => {
    if (!resolvingIssueItem || !org?.id || !db) return;
    const itemId = resolvingIssueItem.id;
    // Determine new status based on outcome
    let newStatus: ItemStatus = 'Delivered'; // Default
    if (outcome === 'Returned to Supplier' || outcome === 'Disposed Of') {
      newStatus = 'Archived';
    }
    // Could add more outcomes mapping to statuses

    try {
      const itemRef = doc(db, 'orgs', org.id, 'stockItems', itemId);
      await updateDoc(itemRef, {
        currentStatus: newStatus,
        lastUpdated: serverTimestamp(),
      });
      // Log action
      const itemBefore = allItems.find(item => item.id === itemId);
      console.log('handleSaveResolveIssue: Attempting to log action. logAction available?', !!logAction);
      if (logAction) {
        await logAction({
          orgId: org.id,
          toolKey: 'incomingtool',
          action: 'item.update.issue.resolve', // Standardized action name
          entityPath: `stockItems/${itemId}`,
          before: itemBefore,
          after: { ...itemBefore, currentStatus: newStatus }, // Approximate state after update
          details: { resolutionOutcome: outcome, note }
        });
      }
      setResolvingIssueItem(null); // Close modal
    } catch (err) {
      setError('Failed to resolve issue: ' + (err as Error).message);
    }
  }, [resolvingIssueItem, org?.id, allItems, logAction, setResolvingIssueItem, setError]);

  // Handler for Saving (Create or Update)
  const handleCloseFormModal = useCallback(() => {
    setIsAddItemModalOpen(false);
    setEditingItem(null); // Clear editing state when closing
  }, [setIsAddItemModalOpen, setEditingItem]);

  const handleSaveItem = useCallback(async (itemData: Omit<StockItem, 'id' | 'currentStatus' | 'dateDelivered' | 'activityLog'> & { isFlagged: boolean }) => {
    console.log('ToolApp: current editingItem at save', editingItem);
    if (!org?.id || !db) {
      console.error('ToolApp: Cannot save item because org.id or db is undefined', org);
      setError('Cannot save item: No organization selected or database unavailable.');
      return;
    }
    console.log('ToolApp: handleSaveItem called', itemData);
    try {
      if (editingItem && editingItem.id) {
        // --- UPDATE LOGIC ---
        const itemId = editingItem.id;
        // Determine changed fields for the EDIT event
        const changedFields = (Object.keys(itemData) as Array<keyof typeof itemData>).filter(key =>
          editingItem.hasOwnProperty(key) && editingItem[key] !== itemData[key]
        );
        // Ensure isFlagged is always considered if it exists in itemData
        if ('isFlagged' in itemData && !changedFields.includes('isFlagged') && editingItem.isFlagged !== itemData.isFlagged) {
             changedFields.push('isFlagged');
        }

        const editEvent = createActivityEvent(user?.uid || '', 'EDITED', { changedFields: changedFields.length > 0 ? changedFields : undefined });

        // Use updateItemAndLog for the actual update and central logging
        await updateItemAndLog(itemId, user?.uid, itemData, editEvent);

      } else {
        // --- CREATE LOGIC ---
        const newItem = removeUndefinedFields({
          ...itemData,
          currentStatus: 'Pending Delivery',
          lastUpdated: serverTimestamp(),
        });
        const docRef = await addDoc(collection(db, 'orgs', org.id, 'stockItems'), newItem);

        // Log create action separately (as updateItemAndLog needs an existing item)
        console.log('handleSaveItem: Attempting to log action. logAction available?', !!logAction);
        if (logAction) {
          await logAction({
            orgId: org.id,
            toolKey: 'incomingtool',
            action: 'item.create',
            entityPath: `stockItems/${docRef.id}`,
            after: newItem // Log the data that was added
            // 'before' is omitted for create actions
          });
        }
      }
      handleCloseFormModal(); // Close modal on success
    } catch (err) {
      console.error('Failed to save item', err);
      setError('Failed to save item: ' + (err as Error).message);
      // Optionally, don't close the modal on error
    }
  }, [editingItem, org, user, logAction, updateItemAndLog, handleCloseFormModal, setError]);

  // Handler to close the item details modal
  const handleCloseDetailModal = useCallback(() => {
    setSelectedItemDetails(null); // Clear state to hide the modal
  }, [setSelectedItemDetails]);

  // Ref to store the current org ID to avoid useCallback dependency issues
  const orgIdRef = useRef<string | undefined>(undefined); // Initialize with undefined

  // Effect to update the orgIdRef whenever the org prop changes
  useEffect(() => {
    orgIdRef.current = org?.id;
  }, [org]);

  // Handler to open the Add Item modal (clears any editing state)
  const handleAddItem = useCallback(() => {
    // Use the ref to get the current org ID
    const currentOrgId = orgIdRef.current;
    if (!currentOrgId) {
      setError("Cannot add item: Organization ID is missing.");
      return;
    }
    // Clear any existing edit state and open add-item modal
    setEditingItem(null);
    setIsAddItemModalOpen(true);
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [setIsAddItemModalOpen, setEditingItem, setError]);

  // *** RESTORED HANDLERS END ***

  // Handler for column drag-and-drop reordering
  const handleColumnDragEnd = (event: DragEndEvent) => {
    const { active, over } = event;

    if (over && active.id !== over.id) {
      setVisibleColumnIds((currentIds) => {
        const oldIndex = currentIds.indexOf(active.id as ColumnConfig['id']);
        const newIndex = currentIds.indexOf(over.id as ColumnConfig['id']);
        // Use arrayMove to update the order
        return arrayMove(currentIds, oldIndex, newIndex);
      });
    }
  };

  // Handler for saving column visibility changes from modal
  const handleSaveColumnSettings = (idsFromModal: Array<ColumnConfig['id']>) => {
    setVisibleColumnIds(currentVisibleIds => {
        const modalIdSet = new Set(idsFromModal);

        // 1. Filter current IDs to keep only those still checked in the modal (preserves order)
        const keptOrderedIds = currentVisibleIds.filter(id => modalIdSet.has(id));

        // 2. Find IDs that are in the modal list but *not* in the current visible list (newly checked)
        const newlyAddedIds = idsFromModal.filter(id => !currentVisibleIds.includes(id));

        // 3. Combine the kept ordered IDs with the newly added ones
        const newVisibleOrder = [...keptOrderedIds, ...newlyAddedIds];

        // Ensure 'actions' column is always present if it was somehow removed (optional safeguard)
        // if (!newVisibleOrder.includes('actions') && ALL_COLUMNS.some(c => c.id === 'actions')) {
        //     newVisibleOrder.push('actions');
        // }

        return newVisibleOrder;
    });
    setIsSettingsModalOpen(false);
  };

  // Ref for search input
  const searchInputRef = useRef<HTMLInputElement>(null);

  // Handler for viewing item details from StockProcessingList
  const handleViewDetails = useCallback((itemId: string) => {
    const item = allItems.find(i => i.id === itemId);
    if (!item) {
      console.error("Item not found for viewing details:", itemId);
      setError('Item not found.'); // Use setError state setter
      return;
    }
    console.log("Viewing details for:", item);
    if (!org) {
      console.error("Org ID is missing, cannot fetch logs.");
      setError('Organization context is missing.'); // Use setError state setter
      return;
    }
    setSelectedItemDetails(item); // Use setSelectedItemDetails state setter
    // TODO: Focus management - consider focusing the modal or close button
  }, [org, allItems, setError]); // Update dependencies

  // Handler for saving a general note
  const handleSaveNote = useCallback(async (itemId: string, note: string) => {
    if (!user?.uid) {
      console.error('User ID is missing, cannot save note.');
      setError('User ID is missing, cannot save note.');
      return;
    }
    console.log(`Saving general note for item ${itemId}: ${note}`);
    try {
      // Create a NOTE_ADDED event
      const event = createActivityEvent(user.uid, 'NOTE_ADDED', { note });
      
      // Call updateItemAndLog with an empty update object, as we're only adding to the log
      // NOTE: updateItemAndLog currently requires an update object. 
      // If we want to *only* log without updating fields, we might need to adjust it
      // or call logAction directly. For now, sending an empty update is harmless.
      await updateItemAndLog(itemId, user.uid, {}, event);
      
      console.log(`General note saved and logged for item ${itemId}`);
      setItemForNote(null); // Close the modal by clearing the item context
    } catch (e) {
      console.error(`Error saving general note for item ${itemId}:`, e);
      setError(`Failed to save note: ${(e as Error).message}`);
    }
  }, [user?.uid, updateItemAndLog]);

  // State for managing the action menu
  const [openMenuId, setOpenMenuId] = useState<string | null>(null);
  const [menuAnchorEl, setMenuAnchorEl] = useState<null | HTMLElement>(null);

  // Handlers for the action menu
  const handleMenuOpen = (event: React.MouseEvent<HTMLElement>, itemId: string) => {
    setMenuAnchorEl(event.currentTarget);
    setOpenMenuId(itemId);
  };

  const handleMenuClose = () => {
    setMenuAnchorEl(null);
    setOpenMenuId(null);
  };

  // Function to render cell content as MUI TableCell
  const renderCellContent = (item: StockItem, columnId: keyof StockItem | 'actions' | 'isFlagged', handleActionClick: (event: React.MouseEvent<HTMLElement>) => void) => {
    const value = item[columnId as keyof StockItem];
    const textOverflowStyle = {
      overflow: 'hidden',
      textOverflow: 'ellipsis',
      whiteSpace: 'nowrap',
      display: 'block', // Needed for Tooltip and overflow to work correctly
    };

    const wrapWithTooltip = (content: React.ReactNode, fullValue: string | number | undefined | null) => (
      <Tooltip title={String(fullValue ?? '')} placement="bottom-start" enterDelay={500}>
        <Box component="span" sx={textOverflowStyle}>
          {content}
        </Box>
      </Tooltip>
    );

    if (columnId === 'actions') {
      return (
        <IconButton
          aria-label="actions"
          size="small"
          // Wrap handler to stop propagation
          onClick={(e) => {
            e.stopPropagation();
            handleActionClick(e);
          }}
        >
          <MoreVertIcon fontSize="inherit" />
        </IconButton>
      );
    } else if (columnId === 'currentStatus') {
      return (
        <Typography variant="body2" sx={{ fontWeight: 'medium', whiteSpace: 'nowrap' }}>
          {item.currentStatus}
        </Typography>
      );
    } else if (columnId === 'isFlagged') {
      return item.isFlagged ? <FlagIcon color="error" fontSize="small" /> : null;
    }

    let content: React.ReactNode;
    if (columnId === 'orderDate' && value) {
      try {
        content = new Date(value as string).toLocaleDateString();
      } catch (e) {
        content = String(value ?? '-'); // Fallback to string if parsing fails
      }
    } else if (columnId === 'pricePerItem' && typeof value === 'number') {
      content = value.toFixed(2); // Format price to 2 decimal places
    } else {
      content = value === undefined || value === null || value === '' ? '-' : String(value);
    }

    const columnsNeedingTooltip = ['seller', 'deliveryName', 'productName', 'acquisitionNotes', 'asinSku', 'orderNumber', 'destination'];
    if (columnsNeedingTooltip.includes(columnId)) {
      return wrapWithTooltip(content, value);
    }

    return <Box component="span" sx={{ whiteSpace: 'nowrap' }}>{content}</Box>;
  };

  // --- Drag and Drop Sensors Setup ---
  const sensors = useSensors(
    useSensor(PointerSensor),
    useSensor(KeyboardSensor, {
      coordinateGetter: sortableKeyboardCoordinates,
    })
  );

  // Define background colors based on item status for row highlighting
  const statusColors: Record<ItemStatus, string> = {
    // Keys must exactly match the ItemStatus type definition
    "Pending Delivery": '#fffde7', // Light yellow
    "Delivered": '#e3f2fd',        // Light blue (Use for delivered)
    "Issue": '#ffebee',            // Light red (Use for issue)
    "Late": '#fff3e0',             // Light orange/amber (Use for late)
    "Archived": '#f5f5f5',        // Light grey
  };

  // --- Placeholder Components (Restored) ---
  const SearchBar = ({ onSearch, inputRef }: { onSearch: (term: string) => void, inputRef?: React.RefObject<HTMLInputElement | null> }) => (
    <TextField
      inputRef={inputRef} // Pass the ref
      fullWidth // Take up available width
      variant="outlined" // Standard MUI style
      placeholder="Search by Delivery Name, Product Name, Seller, or ASIN/SKU..."
      onChange={e => onSearch(e.target.value)}
      size="small" // Make search bar compact
      sx={{ mr: 2, flexGrow: 1 }} // Add margin-right and allow flexible growth
    />
  );

  const FilterTabs = ({ currentFilter, onFilterChange }: { currentFilter: ItemStatus | 'All'; onFilterChange: (filter: ItemStatus | 'All') => void }) => {
    const filters: (ItemStatus | 'All')[] = ['All', 'Pending Delivery', 'Delivered', 'Issue', 'Archived'];

    const handleChange = (event: React.SyntheticEvent, newValue: ItemStatus | 'All') => {
      onFilterChange(newValue);
    };

    return (
      <Box sx={{ borderBottom: 1, borderColor: 'divider', mb: 2 }}>
        <Tabs
          value={currentFilter}
          onChange={handleChange}
          variant="scrollable"
          scrollButtons="auto"
          aria-label="Stock item status filter tabs"
        >
          {filters.map(filter => (
            <Tab key={filter} label={filter} value={filter} />
          ))}
        </Tabs>
      </Box>
    );
  };

  return (
    <Box sx={{ display: 'flex', flexDirection: 'column', height: '100vh', p: 3 }}> {/* Main flex container */}
      {/* Top Bar */} 
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2, flexShrink: 0 }}>
        {/* H2 Title Removed - Provided by Shell */}
        <Box sx={{ flexGrow: 1 }}></Box> {/* Add an empty Box to push buttons to the right */} 
        <div>
          {/* Add Item Button (Moved Here) */} 
          <Button variant="contained" startIcon={<AddIcon />} onClick={handleAddItem} sx={{ mr: 1 }}> 
            Add Item
          </Button>
          {/* Add button to open User Activity View */} 
          <Button variant="outlined" onClick={() => setIsUserActivityViewOpen(true)} sx={{ mr: 1 }}>Activity</Button> {/* Shorten Label */} 
          <Button variant="outlined" onClick={() => setIsSettingsModalOpen(true)} startIcon={<Settings />}>Settings</Button> {/* Shorten Label */} 
        </div>
      </Box>

      {/* Error/Loading Messages */} 
      {error && <Alert severity="error" onClose={() => setError(null)} sx={{ mb: 2, flexShrink: 0 }}>{error}</Alert>}
      {loading && <LinearProgress sx={{ mb: 2, flexShrink: 0 }} />} 
 
      {/* Search and Flag Toggle Controls */} 
      <Box sx={{ display: 'flex', alignItems: 'center', mb: 1, flexShrink: 0 }}> {/* Reduced bottom margin */} 
        {/* Search bar takes available space */} 
        <Box sx={{ flexGrow: 1, mr: 2 }}> {/* Add margin to the right */} 
           {/* Replace missing SearchBar with controlled TextField */}
           <TextField
             fullWidth // Take up available space
             variant="outlined"
             size="small"
             placeholder="Search by Delivery Name, Product Name, Seller, or ASIN/SKU..."
             value={searchTerm} // Controlled component value
             onChange={(e) => setSearchTerm(e.target.value)} // Update state on change
            //  InputProps={{ // Optional: Add search icon
            //    startAdornment: (
            //      <InputAdornment position="start">
            //        <SearchIcon />
            //      </InputAdornment>
            //    ),
            //  }}
           />
         </Box>
         {/* Group label and toggle */} 
         <Box sx={{ display: 'flex', alignItems: 'center', ml: 2 }}> {/* Align items and add margin left */} 
           {/* Replace custom toggle with MUI FormControlLabel and Checkbox */}
           <FormControlLabel 
             control={
               <Checkbox 
                 checked={showFlaggedOnly} 
                 onChange={(e) => setShowFlaggedOnly(e.target.checked)} 
                 size="small" 
               />
             }
             label={<Typography variant="body2">Show Flagged Only</Typography>}
             sx={{ mr: 0 }} // Adjust margin if needed
           />
         </Box>
       </Box>

      {/* Filter Tabs (Now on its own line) */} 
      <Box sx={{ flexShrink: 0, mb: 2 }}> {/* Ensure tabs don't shrink and add margin */} 
        <FilterTabs currentFilter={currentFilter} onFilterChange={handleFilterChange} />
      </Box>

      {/* Stock List Display */} 
      <DndContext
        sensors={sensors}
        collisionDetection={closestCenter}
        onDragEnd={handleColumnDragEnd} // Use ToolApp handler
        modifiers={[restrictToHorizontalAxis]}
      >
        {/* Table container grows to fill space */} 
        <TableContainer component={Paper} sx={{ flexGrow: 1, overflow: 'auto' }}>
          <Table stickyHeader sx={{ minWidth: 650 }} aria-label="stock processing table" size="small">
            <SortableContext items={visibleColumns.map(c => c.id)} strategy={horizontalListSortingStrategy}>
              <TableHead>
                <TableRow>
                  {visibleColumns.map(column => (
                    <DraggableHeader key={column.id} column={column} />
                  ))}
                </TableRow>
              </TableHead>
            </SortableContext>
            <TableBody>
              {/* Ensure mapping over filteredAndSortedItems */} 
              {filteredItems.length > 0 ? filteredItems.map((item, idx) => {
                const isSelected = idx === selectedRowIndex;
                return (
                  <TableRow 
                    key={item.id}
                    hover
                    onClick={() => handleViewDetails(item.id)}
                    selected={isSelected}
                    sx={{
                      cursor: 'pointer',
                      backgroundColor: statusColors[item.currentStatus] || 'inherit',
                      '&:hover': { backgroundColor: '#e0e0e0' }, // Darker hover for better contrast with dense table
                      // Match header widths for consistency, including text overflow style
                      '& .MuiTableCell-root': {
                        overflow: 'hidden',
                        textOverflow: 'ellipsis',
                        whiteSpace: 'nowrap',
                      }
                    }}
                  >
                    {visibleColumns.map((col) => {
                      // Determine alignment based on column ID (similar to header)
                      const isNumeric = ['quantity', 'pricePerItem'].includes(col.id);
                      return (
                        <TableCell 
                          key={col.id} 
                          align={isNumeric ? 'right' : 'left'} 
                          // Optionally add padding overrides if needed, but rely on size='small'
                        >
                          {/* Call renderCellContent, passing the correct action handler */}
                          {renderCellContent(
                            item, 
                            col.id as keyof StockItem, 
                            // Pass inline function calling handleMenuOpen with item.id captured from closure
                            (event) => handleMenuOpen(event, item.id) 
                          )} 
                        </TableCell>
                      );
                    })}
                  </TableRow>
                );
              }) : (
                <TableRow>
                  <TableCell colSpan={visibleColumns.length} align="center" sx={{ py: 3 }}>
                    No items match the current filter or none loaded yet.
                  </TableCell>
                </TableRow>
              )}
            </TableBody>
          </Table>
        </TableContainer>
      </DndContext>
      
      {/* Render Add/Edit Item Form Modal */}
      {isAddItemModalOpen && (
        <AddItemForm 
          onSave={handleSaveItem} // Use combined save handler
          onClose={handleCloseFormModal} // Use combined close handler
          initialData={editingItem} // Pass item to edit (null if adding)
        />
      )}

      {/* Render Item Details Modal */}
      {selectedItemDetails && (
        <ItemDetailModal 
          item={selectedItemDetails} 
          orgId={org.id} 
          onClose={handleCloseDetailModal} 
          onActionSelected={handleItemAction}
        />
      )}

      {/* New Modals for Issue Handling */}
      {reportingIssueItem && (
        <ReportIssueModal 
          item={reportingIssueItem} // Pass the item
          onSubmit={handleSaveReportIssue} 
          onClose={() => setReportingIssueItem(null)} 
        />
      )}
      {addingUpdateItem && (
        <AddUpdateModal 
          item={addingUpdateItem} // Pass the item context
          onSubmit={handleSaveIssueUpdate} 
          onClose={() => setAddingUpdateItem(null)} 
        />
      )}
      {resolvingIssueItem && (
        <ResolveIssueModal 
          item={resolvingIssueItem} // Pass the item
          onSubmit={handleSaveResolveIssue} 
          onClose={() => setResolvingIssueItem(null)} 
        />
      )}
      {itemForNote && (
        <AddUpdateModal 
          item={itemForNote} // Pass the item context
          onSubmit={(note) => handleSaveNote(itemForNote.id, note)} // Pass the specific item ID and note to the correct handler
          onClose={() => setItemForNote(null)} // Close by clearing item context
        />
      )}

      {/* Render Table Settings Modal */}
      <TableSettingsModal
        isOpen={isSettingsModalOpen}
        onClose={() => setIsSettingsModalOpen(false)}
        allColumns={ALL_COLUMNS}
        initialVisibleColumns={visibleColumns} // Pass current derived configs
        onSave={handleSaveColumnSettings} // Handler updates visibleColumnIds state
      />
      {/* Render User Activity View Modal */}
      {isUserActivityViewOpen && org?.id && (
        <UserActivityView
          orgId={org.id}
          onClose={() => setIsUserActivityViewOpen(false)}
          onEntityClick={handleViewDetails}
          onActionSelected={handleItemAction}
        />
      )}
      
      {/* Action Menu controlled by ToolApp state */}
      <ActionMenu
        itemId={openMenuId || ''}
        // Ensure correct item source for actions
        actions={openMenuId ? getAvailableActions(filteredItems.find(i => i.id === openMenuId)?.currentStatus ?? 'Pending Delivery', filteredItems.find(i => i.id === openMenuId)?.isFlagged ?? false) : []}
        onActionSelected={handleItemAction} // Use ToolApp handler
        onClose={handleMenuClose} // Use ToolApp handler
        anchorEl={menuAnchorEl}
        open={Boolean(openMenuId)}
      />
    </Box>
  );
}

export default ToolApp;